* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  direction: rtl;
}

body {
  background-color: #292524;
  color: white;
  padding: 28px 64px 0px;
  font-family: "Secular One", sans-serif;
}

#root {
  max-width: 1500px;
  margin-left: auto;
  margin-right: auto;
  direction: rtl;
}

.hidden {
  display: none !important;
}

header {
  margin-bottom: 40px;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.subhead {
  font-size: 22px;
}

.message {
  text-align: center;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 600;
  margin-top: 10px;
}

.middle {
  display: flex;
  align-items: center;
  gap: 16px;
  flex-direction: column;
  margin-left: 60px;
}

.logo {
  display: flex;

  flex-direction: column;
}
h1 {
  font-size: 42px;
  text-transform: uppercase;
  font-family: "Secular One", sans-serif;
  color: white;
  margin-top: 6px;
  line-height: 1;
}

h2 {
  font-size: 16px;
  font-weight: 300;
}

.btn a {
  text-decoration: none;
  color: black;
}

.career {
  margin-bottom: 20px;
  padding: 10px;
  width: 23%;
  color: #000000;
  font-size: 20px;
  font-family: "Secular One", sans-serif;
  text-align: center;
  text-transform: uppercase;

}

.career-btn {
  margin: 0 auto;
  width: 60%;
  height: 100%;
  font-size: 20px;
  font-family: "Secular One", sans-serif;
}

.confession-input {
  resize: none;
  height: 140px;
  width: 100%;
  box-sizing: border-box;
  padding: 26px;
  font-size: 14px;
}

/* .vote-btn.voted {
  background-color: #292524;
  color: #ffffff;
} */



.vote-btn.active {
  background-color: #292524;
  color: #ffffff;
}
.vote-btn.dis-active {
  background-color: #292524;
  color: #ffffff;
}



.category {
  margin-bottom: 16px;
}

.form-career {
  padding: 17px 40px;
}

/*  */

.fact {
  font-size: 20px;
  line-height: 1.4;
  font-family: "Secular One", sans-serif;
  color: white;
  color: black;
  /* background-color: #f2e8cf; */
  background-color: white;
  margin-bottom: 16px;
  padding: 16px 24px;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  width: 75%;
  gap: 7px;
}

@media (max-width: 1200px) {
  .fact {
    flex-direction: column;
    gap: 18px;
    width: 100%;
    float: right;
    font-size: 18px;
  }

  .search-container {
    display: none;
  }
  .search-input {
    display: none;
  }
}

section {
  display: block;
}

.main {
  display: grid;
  grid-template-columns: 200px 1fr;
  gap: 48px;
  /* height: calc(100vh - 48px - 68px - 40px); */
}

.homeHeading {
  text-decoration: none;
  color: white;
}

@media (max-width: 900px) {
  .main {
    grid-template-columns: 200px 1fr;
  }

  body {
    padding: 28px 24px 0px;
  }

  .search-container {
    display: none;
  }
  .search-input {
    display: none;
  }
}

.vote {
  flex-shrink: 0;
  display: flex;
  gap: 8px;
}

.vote button {
  background-color: #f2e8cf;
  border: 2px solid #000000;
  font-size: 14px;
  padding: 6px 12px;
  color: inherit;
  font-family: inherit;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
  border-radius: 60px;
}

.upvote {
  color: #07ff49;
  font-size: 17px;
  margin-bottom: -3px;
}
.downvote {
  color: #ff2007;
  font-size: 17px;
  margin-bottom: -4px;
}

.suprised {
  color: #ffee07;
  font-size: 17px;
  margin-bottom: -4px;
}

.vote button:hover {
  background-color: #292524;
}
.vote button:disabled {
  background-color: #44403c;
}

.tag {
  text-transform: uppercase;
  font-size: 14px;
  font-family: "Secular One", sans-serif;
  font-weight: 500;
  padding-top: 3px;
  padding-left: 10px;
  padding-right: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  width: fit-content;
  color: white;
}

.role {
  font-weight: 700;
}

.disputed {
  color: #ef4444;
  font-weight: 600;
  margin-right: 10px;
}

.pages-btns {
  margin: auto;
  padding: 10px;
  width: 63%;
}

.btn-page {
  padding: 10px;
  border: none;
  background-color: #ffffff;
  color: #000000;
  font-size: 20px;
  font-family: "Secular One", sans-serif;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
  border-radius: 60px;
  margin: 10px;
}
.btn-page:hover {
  background-color: #292524;
  color: white;
}

ul {
  list-style: none;
}

.source {
  color: rgb(168, 164, 164);
  text-decoration: none;
  margin-left: 10px;
  transition: 1s;
}

.source:hover {
  color: #3b82f6;
}

.btn {
  border: none;
  border-radius: 50px;
  font-family: "Secular One", sans-serif;
  line-height: 1;
  text-transform: uppercase;
  font-size: 17px;
  padding: 16px 0 13px;
  /* background-image: linear-gradient(135deg, #3b82f6, #ef4444,
    #16a34a,#eab308); */
  background-color: #a7c957;
  background-color: #ffffff;
  color: #000000;
  transition: 0.3;
  cursor: pointer;
}

.btn:hover {
  transform: scale(110%) rotate(-2deg);
}

.btn-large {
  font-size: 20px;
  padding: 20px 32px 17px;
}

.sendSalary {
  width: 50%;
  margin: auto;
}

.btn-all {
  margin-bottom: 28px;
  width: 100%;
}

.btn-category {
  width: 100%;
  background-image: none;
  color: #ffffff;
}

.share-text {
  font-size: 18px;
}

.salary-page-link {
  text-decoration: none;
  color: #000000;
}

.home-page-back-btn {
  padding: 10px;
  border: none;
  background-color: #ffffff;
  color: #000000;
  font-size: 20px;
  font-family: "Secular One", sans-serif;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
  border-radius: 60px;
  margin: 10px;
}

.fact-form {
  display: flex;
  flex-direction: column;
  gap: 12px;
  border-radius: 16px;
  padding: 7px 50px;
  background-color: #44403c;
  margin-bottom: 20px;
  /* center the form */
  margin-left: auto;
  margin-right: auto;
  width: 90%;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
}

textarea {
  background-color: #78716c;
  border: none;
  border-radius: 16px;
  padding: 16px 40px;
  font-size: 16px;
  font-family: inherit;
  color: white;
  width: 100%;
}

.fact-form input,
.fact-form select {
  width: 100%;
  background-color: #78716c;
  border: none;
  border-radius: 16px;
  padding: 14px;
  font-size: 16px;
  color: inherit;
  font-family: inherit;
}

.fact-form input::placeholder {
  color: #a8a29e;
}
.fact-form textarea::placeholder {
  color: #a8a29e;
}
.fact-form input:first-child {
  flex-grow: 1;
}

option {
  font-family: "Secular One", sans-serif;
}

@media (max-width: 1000px) {
  body {
    /* padding: 32px 48px; */
    direction: rtl;
  }

  .search-container {
    display: none;
  }
  .search-input {
    display: none;
  }

  .fact-form {
    display: flex;
    flex-direction: column;
    gap: 18px;
    width: 100%;
  }

  .form-row {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  .fact-form input,
  .fact-form select,
  .fact-form textarea {
    display: flex;
  }
}
@media (max-width: 900px) {
  .main {
    grid-template-columns: 1fr;
    height: auto;
  }
  .search-container {
    display: none;
  }
  .search-input {
    display: none;
  }
}

@media (max-width: 600px) {
  #root {
    /* padding: 24px; */
  }

  .header {
    flex-direction: column;
    gap: 16px;
  }

  .fact-form {
    padding: 16px;
  }

  .fact {
    /* padding: 12px 16px; */
    width: 100%;
  }

  .search-container {
    display: none;
  }
  .search-input {
    display: none;
  }

  h1 a {
    font-size: 22px;
  }

  h2 {
    font-size: 12px;
  }

  .header-social {
    display: block;
  }
}

.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  padding: 7px;
  border-radius: 10px;
  background-color: #44403c;
}

.open-modal-btn {
  padding: 10px;
  border: none;
  background-color: #ffffff;
  color: #000000;
  font-size: 20px;
  font-family: "Secular One", sans-serif;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
  border-radius: 60px;
  margin: 10px;
}

.close-btn {
  padding: 10px;
  border: none;
  background-color: #ffffff;
  color: #000000;
  font-size: 20px;
  font-family: "Secular One", sans-serif;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
  border-radius: 60px;
  margin: 10px;
}

.close-btn:hover {
  background-color: #292524;
  color: white;
}

.search-input {
  padding: 10px;
  border: none;
  background-color: #ffffff;
  color: #000000;
  font-size: 20px;
  font-family: "Secular One", sans-serif;
  font-weight: 600;

  transition: 0.3s;
  border-radius: 60px;
  margin: 10px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  display: inline-block;
}

.search-container {
  display: flex;
  align-items: center;
}

.search-icon {
  position: absolute;
  translate: 50%;
  left: 58%;
  right: 58%;
  color: #000000;
  font-size: 20px;
  font-family: "Secular One", sans-serif;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
  border-radius: 60px;
  margin-top: 10px;
}

.captcha {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}

.socialbtn a {
  display: inline-flex;
  /* width: 50px; */
  color: #ffffff;
  margin: 0 6px;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
  font-size: 22px;
  cursor: pointer;
  margin-bottom: 10px;
}

.socialbtn a:hover {
  background: #44403c;
  color: white;
}

.about-text .socialbtn a {
  color: #292524;
}
.about-text .socialbtn {
  text-align: center;
}
.about-text .socialbtn a:hover {
  background-color: #ffffff;
}

.header-social {
  display: inline;
  font-size: 25px;
  margin-right: 15px;
}

.header-social a {
  gap: 20px;
  margin-left: 5px;
  margin-top: -20px;
}

.header-social a:hover {
  color: #000000;
}

.footer {
  /* footer design */
  background-color: #44403c;
  color: #f2e8cf;
  font-family: "Rubik", sans-serif;
  font-size: 14px;
  text-align: center;
  margin-top: 20px;
  font-weight: 200;
  padding: 20px;
  border-radius: 20px;
}

@media (max-width: 767px) {
  .search-container {
    display: none;
  }
  .search-input {
    display: none;
  }
  .subhead {
    font-size: 8px;
  }

  .message {
    font-size: 8px;
  }
  h1 {
    font-size: 24px;
  }
  .email {
    margin-right: -50px;
    font-size: 12px;
  }
}

