.about-section {
  margin: 0 auto;
  width: 100%;
  
}

.about-text {
  font-size: 20px;
  line-height: 1.8;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  color: white;
  padding: 30px;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  width: 80%;
  margin: 0 auto;
  margin-top: -20px;
  background-color: #212121;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.4);
}


.about-text h1,
.about-text h2 {
  font-size: 30px;
  font-weight: 700;
  margin: 0;
  text-align: center;
}

.about-text p {
  font-size: 20px;
  line-height: 1.8;
  margin-top: 30px;
}

.about-text a {
  color: #64ffda;
  text-decoration: none;
  transition: all 0.2s ease-in-out;
}

.about-text a:hover {
  color: #00b0ff;
}

.about-text .socialbtn a {
  color: white;
  font-size: 24px;
  margin-right: 15px;
  transition: all 0.2s ease-in-out;
}

.about-text .socialbtn a:hover {
  transform: translateY(-5px);
}







